.App{
  text-align: center;
}

.cart{
  background-color: rgb(188, 142, 235);
  border: 2px solid black;
  width: 290px;
  padding: 10px;
  margin: 20px;
  border-radius: 0px 40px 40px 40px;
}

.cart ul li{
  list-style: none;
}
.cart button{
  margin-right: 20px;
  border-radius: 5px;
}
.cart a{
  color: black;
  border: 2px solid black;
  border-radius: 5px;
}
#btn2{
  background-color: rgb(252, 4, 4);
  margin: 10px;
}
#btn1{
  background-color: rgb(232, 241, 104);
}

.allcart{
  background-color: white;
  display: flex;
  height: 100vh;
  flex-direction: column;
  text-align: center;
  padding: 20px;
}

.cartcontainer{
  display: flex;
  flex-wrap: wrap;
  background-color: rgb(4, 37, 4);
  justify-content: center;
  border-radius: 20px;
}

.navtoggle{
  display: flex;
  gap: 30px;
}
.Navbar{
  background-color: rgb(214, 203, 203) !important
}